<template>
  <div
    class="lang-box"
    :style="{
      backgroundImage: `url('${imgurl}')`,
    }"
    @click="change"
  ></div>
</template>

<script>
  import { mapState, mapMutations } from 'vuex'

  import langZh from '@/assets/images/icon/lang_zh.png'
  import langEn from '@/assets/images/icon/lang_en.png'

  export default {
    data(){
      return{
        lang:'zh-CN',
        imgurl:langZh
      }
    },
    computed: {
      // ...mapState(['lang']),
      // img() {
      //   return this.lang == 'zh-CN' ? langZh : langEn
      // },
    },
    created(){
      if(localStorage.getItem('lang')==undefined){
        localStorage.setItem('lang', 'zh-CN')
        this.imgurl=langZh
      }else{
        this.lang=localStorage.getItem('lang')
      }
    },
    methods: {
      // ...mapMutations(['SET_LANG']),
      change() {
        if (this.lang == 'zh-CN') {
            localStorage.removeItem('lang')
           localStorage.setItem('lang', 'en')
           this.imgurl=langEn
           this.lang = 'en'
        } else {
           localStorage.removeItem('lang')
           localStorage.setItem('lang','zh-CN')
           this.lang = 'zh-CN'
           this.imgurl=langZh

        }
        // window.location.reload()
      },
    },
  }
</script>

<style lang="scss" scoped>
  .lang-box {
    width: 113px;
    height: 55px;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
  }
</style>
