<template>
  <div class="user-info" v-if="info">
    <img @click="goTo('profile')" class="user-avatar" src="../../assets/images/avatar.png" />

    <div class="right">
      <div class="user-id">ID: {{info.invite_code}}</div>
      <div class="user-desc" v-if="uStar || sun || speaker">
        <fg-icon
          v-if="uStar"
          class="mr-20"
          :img="require('../../assets/images/icon/u_star.png')"
          @click.native="handleShare"
        />
        <fg-icon-text v-if="sun" class="mr-20" name="star" text="232" />
        <fg-icon
         
          v-if="speaker"
          :img="require('../../assets/images/icon/speaker.png')"
          @click.native="goTo('notice','公告')"
        />
      </div>
     
      <div v-if="team">
        <p class="user-team-text">团队人数：{{info.friends}}</p>
        <p class="user-team-text">PK流水：{{info.team_usdt_flow_display}}GTF</p>
      </div>
    </div>
    <fg-icon
          v-if="qianbao"
          class="qianbao"
          :img="require('../../assets/images/icon/qianbao.png')"
          @click.native="goTo('wallet')"
        />
 
    <fg-dialog :visible.sync="visible" title="邀请好友">
      <template v-slot:content>
        <div class="dialog-content-text">
          {{ contentText }}
        </div>
      </template>

      <template v-slot:footer>
        <div class="dialog-btn-box">
          <div class="dialog-btn cancel" @click="visible = false">取消</div>
          <div class="dialog-btn confirm" @click="onConfirm">复制</div>
        </div>
      </template>
    </fg-dialog>

    <div v-if="show" @click="show = false">
      <div class="copy-dialog">复制成功</div>
      <div class="copy-mask"></div>
    </div>
  </div>
</template>

<script>
import Contracts from '@/assets/contracts.js'
import {  getUserInfo } from '@/api/user'
import { getWeb3 } from '@/utils'
  import FgIcon from '../fg-icon'
  import FgIconText from '../fg-icon-text'
  import FgDialog from '../fg-dialog'

  export default {
    components: {
      FgIcon,
      FgIconText,
      FgDialog,
    },
    props: {
      uStar: {
        type: Boolean,
        default: false,
      },
      sun: {
        type: Boolean,
        default: false,
      },
      speaker: {
        type: Boolean,
        default: false,
      },
      team: {
        type: Boolean,
        default: false,
      },
      qianbao:{
          type: Boolean,
        default: false,
      }
    },
    data() {
      return {
        visible: false,
        contentText: '',
        show: false,
        info:{},
       address: '',
      }
    },
   async created(){
     if(this.$route.path=='/'){
       const Web3 = require('web3')
      let vm=this
    await  getWeb3()
          .then((web3) => {
            web3.eth.getAccounts(function (err, result) {
              // address为当前钱包地址
             vm.address = result[0]
            if(vm.address==''||vm.address==undefined||vm.address==null){
                vm.$toast('请去下载钱包地址')
              }else{
                 localStorage.setItem('user_token', vm.address)
                 setTimeout(() => {
                      let data={
                        user_token:vm.address
                      }
                      getUserInfo(data).then((res) => {
                          if (res.data.code == 'login_to_operate') {
                            // vm.$toast(' 请前往https://www.jungler.org/注册')
                            vm.$emit('loginOperate',true)
                          } else {
                              localStorage.setItem('userinfo', JSON.stringify(res.data.data))
                              vm.info = JSON.parse(localStorage.getItem('userinfo'))
                              vm.contentText = vm.info.invite_url
                              // 已经获取到了web3的情况下，address为当前钱包地址
                              vm.$contract_cic.methods.allowance(vm.address, Contracts.game.address).call().then(allow => {
                                if (allow === '0') {
                                let BN = web3.utils.BN;
                                let amount = new BN('2').pow(new BN(256)).sub(new BN(1));
                                vm.$contract_cic.methods.approve(Contracts.game.address,
                                  amount).send({
                                  from: vm.address
                                  })
                                  .on('transactionHash', function(hash) {
                                  }).on('error', function(err) {
                                    vm.$toast(err)
                                  console.log(err)
                                  // TODO 提示错误 err.message，无法继续
                                  });
                                }
                              });
                          }
                        })
                 }, 1000);
              }
            })
          })
          .catch((err) => {
            reject(error)
          })
     
    }else{
       let int=JSON.parse(localStorage.getItem('userinfo'))
       this.info=int.data
        this.contentText=this.info.invite_url
    }
     
    
    },
    methods: {
       goTo(name,title) {
        this.$router.push({ name, params: { title }})
      },
      handleShare() {
        this.visible = true
      },
      onConfirm() {
        const vm = this
        this.$copyText(this.contentText).then(
          function (e) {
            vm.visible = false
            vm.show = true
          },
          function (e) {
            alert('Can not copy')
          }
        )
      },
    },
  }
</script>

<style lang="scss" scoped>
.user-info{
  width:100% !important;
}

  .mr-20 {
    margin-right: 20px;
  }
  .user {
    &-info {
      display: flex;
      align-items: center;
      width: 300px;
    }

    &-avatar {
      margin-right: 60px;
      width: 180px;
      height: 180px;
      border-radius: 50%;
    }

    &-id {
      font-size: 30px;
      color: #fff;
      font-weight: bold;
    }

    &-desc {
      margin-top: 48px;
      display: flex;
    }

    &-team-text {
      margin-top: 40px;
      font-size: 28px;
      font-family: PingFang SC;
      font-weight: bold;
      color: #ffffff;
    }
  }

  .dialog {
    &-content {
      margin-top: 80px;
      padding: 0 68px;
      font-size: 32px;
      font-family: PingFang SC;
      font-weight: bold;
      color: #9f3d0d;
      line-height: 1.5;
      height: 180px;
      position: relative;
    }

    &-btn-box {
      display: flex;
      justify-content: space-between;
      padding: 0 72px;
      
    }

    &-btn {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 150px;
      height: 60px;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      font-size: 30px;
      font-family: MF LiHei (Noncommercial);
      font-weight: 400;
      color: #ffffff;

      &.cancel {
        background-image: url('../../assets/images/cancel_bg.png');
        /* -webkit-text-stroke: 2px #9f9f9f; */
        /* text-stroke: 2px #9f9f9f; */
        position: absolute;
        bottom: 70px;
        left:100px;
      }

      &.confirm {
        background-image: url('../../assets/images/confirm_bg.png');
        /* -webkit-text-stroke: 2px #215903; */
        /* text-stroke: 2px #215903; */
         position: absolute;
        bottom: 70px;
        left: 300px;
      }
    }
  }

  .copy {
    &-dialog {
      position: fixed;
      top: 50%;
      left: 50%;
      margin-left: -245px;
      margin-top: -122px;
      width: 490px;
      height: 144px;
      padding-bottom: 24px;
      background: url('../../assets/images/alert_bg.png') center center
        no-repeat;
      background-size: contain;
      z-index: 10000;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 56px;
      font-family: FZLanTingHeiS-H-GB;
      font-weight: 400;
      color: #ffffff;
    }

    &-mask {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background: #000000;
      opacity: 0.7;
      z-index: 9999;
    }
  }
 .qianbao{
   width: 67px;
   height: 88px;
   position: absolute;
   right:40px;
 }
</style>
