import Vue from 'vue'
import VueI18n from 'vue-i18n'

import en from './lang/en'
import zh from './lang/zh'
import { storage } from '@/utils'

Vue.use(VueI18n)

const messages = {
  en,
  zh,
}

const locale = storage.get('lang') || 'zh-CN'

export default new VueI18n({
  locale,
  messages,
})
