<template>
  <div class="home-container">
    <div class="top-bar">
      <!-- <lang-switch /> -->

      <div class="top-right">
        <fg-icon-text
          v-if="info"
          class="top-right-gold"
          name="gold"
          :text="info.data.candy_display"
        ></fg-icon-text>
        <!-- <fg-icon-text name="star" text="232"></fg-icon-text> -->
      </div>
    </div>

    <user-info class="user-info" @loginOperate='loginOperate'  qianbao/>

    <van-notice-bar
      class="notice-bar"
      color="#9e3a0b"
      :text="texts"
    >
    </van-notice-bar>

    <div class="nav-wrap">
      <van-row gutter="18">
        <van-col span="12" class="nav-col" @click="goTo('buy')"
          ><img class="nav-item-lg" src="../../assets/images/home_buy.png" />
          <span class="nav-name center">{{ $t('menu.buyGuardian') }}</span>
        </van-col>
        <van-col
          span="12"
          class="nav-col"
          @click="goTo('guardian', '我的守卫者')"
          ><img
            class="nav-item-lg"
            src="../../assets/images/home_gua.png"
          /><span class="nav-name center">我的守卫者</span></van-col
        >
      </van-row>

      <van-row gutter="18" class="nav-bot">
        <van-col span="12">
          <div class="nav-col" @click="goTo('trophy')">
            <img class="" src="../../assets/images/home_trophy.png" />
            <span class="nav-name right">战利品</span>
          </div>
          <div class="nav-item-sm nav-col" @click="goTo('shop')">
            <img src="../../assets/images/home_shop.png" />
            <span class="nav-name right">{{ $t('shop') }}</span>
          </div>
        </van-col>
        <van-col span="12" class="nav-col" @click="goTo('arena')"
          ><img src="../../assets/images/home_pk.png" /><span
            class="nav-name center"
            >PK竞技场</span
          ></van-col
        >
      </van-row>
    </div>

    <div class="footer">
      <fg-icon
        class="footer-icon"
        :img="require('../../assets/images/icon/book_blue.png')"
      ></fg-icon>

      <div class="footer-btn" @click="goTo('guardian', '选择守卫者')"></div>

      <fg-icon
        class="footer-icon"
        :img="require('../../assets/images/icon/book_green.png')"
         @click.native="goTo('notice','攻略')"
      ></fg-icon>
    </div>
    <!-- <div v-if="iSmask" class="mask">请前往https://www.jungler.org/注册</div> -->
    <van-overlay :show="iSmask" @click="show = false">
      <div class="wrapper">
        <div class="block" >请前往https://www.jungler.org/注册！！</div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { getWeb3 } from '@/utils'
import {  getUserInfo } from '@/api/user'
import Contracts from '@/assets/contracts.js'
import { scroll_texts} from '@/api/home'
  export default {
    components: {
    },
    data() {
    return {
       texts:'',
       info:'',
       address: '',
       iSmask:false
    };
  },
 created() {
  },
  mounted() {
     this.scroll_texts();
  },
    methods: {
      loginOperate(value){
        console.log(value)
        this.iSmask=value
      },
      goTo(name, title) {
        if(name=='arena'){
          this.$toast('敬请期待！')
          return
        }
       
        this.$router.push({ name, params: { title} })
      },
      scroll_texts(){
       scroll_texts().then((res) => {
         if(res.data.code=='success'){ this.texts=res.data.data.content}else{this.$toast(res.data.msg)}
       
          }).catch((error) => {
            reject(error)
          })
      },
    },
  }
</script>

<style lang="scss" scoped>

  .home-container {
    min-height: 100vh;
    background: url('../../assets/images/home_bg.png') center center no-repeat;
    background-size: 100% 100%;
    padding: 0 40px;
    position: relative;
  }
    .mask{
      position: absolute;
      top:0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.6);
      width: 100%;
      height: 100%;
      z-index: 1000;
    }
  .top {
    &-bar {
      padding-top: 27px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-right {
      display: flex;

      &-gold {
        margin-right: 20px;
      }
    }
  }

  .user-info {
    margin-top: 40px;
  }

  .notice-bar {
    margin-top: 10px;
    background: url('../../assets/images/notice_bg.png') center center no-repeat;
    background-size: 100% 100%;
    height: 130px;
    font-size: 28px;
    font-family: PingFang SC;
    font-weight: bold;
  }

  .nav {
    &-wrap {
      /* margin-top: 10px; */
    }

    &-col {
      position: relative;
    }

    &-name {
      position: absolute;
      color: #c95100;
      font-size: 30px;
      font-family: 'FZLanTingHei-EB-GBK';
      font-weight: bold;

      &.center {
        top: 55px;
        left: 0;
        text-align: center;
        width: 100%;
      }

      &.right {
        top: 24px;
        right: 24px;
      }
    }

    &-item-lg {
      height: 402px;
    }

    &-bot {
      margin-top: 16px;
    }

    &-item-sm {
      margin-top: 18px;
    }
  }

  .footer {
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-icon {
      width: 67px;
      height: 71px;
    }

    &-btn {
      width: 258px;
      height: 100px;
      background: url('../../assets/images/btn_start.png') center center
        no-repeat;
      background-size: 100% 100%;
    }
  }
   .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .block {
    width: 600px;
    height: 120px;
    background-color: #fff;
    color:#c95100;
    border-radius: 25px;
    padding: 40px;
  }
</style>
